// Virgo Toast Component
// ================================================================================================
import { NotificationComponent } from "./NotificationComponent";
import { DefaultNotificationConfig } from "./NotificationConfig";
/**
 * Toast component class.
 */
export class ToastComponent extends NotificationComponent {
    // [3] Component Static Methods
    // --------------------------------------------------------------------------------------------
    // [4] Component Instance Methods
    // --------------------------------------------------------------------------------------------
    constructor(config = DefaultNotificationConfig) {
        let componentConfig = Object.assign(Object.assign({}, DefaultNotificationConfig), { flyer: Object.assign(Object.assign({}, DefaultNotificationConfig.flyer), { x: 20, y: 20, base: "topRight" }) });
        super(componentConfig);
    }
}
// [1] Component Static Properties
// --------------------------------------------------------------------------------------------
ToastComponent.className = "toast";
